        The Service does not knowingly collect personal information from children under 13 years of age. If the Service learns that it has inadvertently gathered personal data from children under 13 years of age, the Service will take reasonable measures to promptly delete such personal data from the Service's records. We do not knowingly collect personally identifiable information from children under 13. If you are a parent or guardian and you are aware that your Children has provided us with Personal Information, please contact us. If we discover that a Children under 13 has provided us with Personal Information, we will delete such information from our servers immediately.
