    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'privacy',
  data () {
    return {
    }
  },
  methods: {
    ...mapActions(['configureTitlebar'])
  },
  mounted () {
    const self = this
    this.configureTitlebar({'Home': () => { self.$router.push('/') }})
  }
}
</script>

<style lang="less" scoped>
.privacy {
}
</style>



